/* Global Styles */
body {
  font-family: 'Red Hat Display', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #121212;
  color: #fff;
}

/* Container for the Hero Section */
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: 4rem;
  max-width: 1200px;
  margin: auto;
  padding: 4rem 2rem;
  position: relative;
  z-index: 10;
}

/* Top Content (Hero Text and Buttons) */
.top-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  position: relative;
  z-index: 10;
  margin-bottom: 2rem;
}

/* Hero Text Styling */
.hero-text {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  gap: 0.5rem;
}

.hero-text h1 {
  font-size: clamp(3rem, 5vw, 5rem);
  color: #ffffff;
  font-weight: 400;
  margin: 0;
  text-shadow: 0 0 10px #000000, 0 0 1px #000000;
}

.hero-text p {
  font-size: clamp(1.2rem, 2vw, 1.5rem);
  color: #fff;
  font-weight: 400;
  margin: 0;
}

/* Button Container */
.button-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  margin-top: 2rem;
}

/* Button Styling */
.button {
  padding: 0.9rem 1.8rem;
  background-color: #000000;
  color: #ffffff;
  border: 2px solid white;
  border-radius: 45px;
  font-size: 1rem;
  font-family: 'Red Hat Display', sans-serif;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 180px;
  text-align: center;
  position: relative;
  overflow: hidden;
}

/* Rainbow glow effect for Resume button border */
.button {
  transform: translateY(-5px);
  animation: rainbow-border 3s linear infinite;
}

@keyframes rainbow-border {
  0% {
    border-color: #ff00ff;

  }

  25% {
    border-color: #00f7ff;

  }

  50% {
    border-color: #00ff88;

  }

  75% {
    border-color: #ff00ff;
    box-shadow: 0 0 1px #ff00ff, 0 0 2px #ff00ff;
  }

  100% {
    border-color: #00f7ff;
    box-shadow: 0 0 1px #00f7ff, 0 0 2px #00f7ff;
  }
}

/* Social Media Icons */
.social-media {
  display: flex;
  gap: 1rem;
}

.social-media img {
  width: 2.5rem;
  height: 2.5rem;
  padding: 0.3rem;
  transition: transform 0.3s ease;
}

.social-media img:hover {
  transform: scale(1.1);
}

/* Projects Section - Vertical Layout */
.boxes-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
  width: 100%;
  margin-top: 2rem;
  position: relative;
  z-index: 10;
}

/* Project Box (Unified Style â€” Formerly "alternate") */
.box {
  background-color: rgba(0, 0, 0, 0.7);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 12px;
  width: 100%;
  max-width: 800px;
  padding: 2rem;
  transition: all 0.4s ease;
  animation: fadeIn 0.8s ease-out;
  backdrop-filter: blur(10px);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
}

.box:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 40px rgba(0, 247, 255, 0.3);
  border-color: #00f7ff;
}

/* Unified Text Styles */
.box-title {
  font-size: 1.8rem;
  color: #ffffff;
  font-weight: 500;
  margin: 0;
}

.box-content {
  font-size: 1.1rem;
  color: #e0e0e0;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.line {
  width: 100%;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.3);
  margin: 1.5rem 0;
}

.sub {
  font-size: 1rem;
  color: #aaaaaa;
  margin-bottom: 1rem;
  font-weight: 300;
}
/* Box Title and Icons */
.box-title-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.box-title {
  font-size: 1.8rem;
  color: #ffffff;
  font-weight: 500;
  margin: 0;
}

.box-icons img {
  width: 32px;
  height: 32px;
  margin-left: 0.8rem;
  object-fit: contain;
}

/* Line Separator */
.line {
  width: 100%;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.2);
  margin: 1.5rem 0;
}

/* Subtitle (Technologies Used) */
.sub {
  font-size: 1rem;
  color: #989898;
  margin-bottom: 1rem;
  font-weight: 300;
}

/* Box Content (Project Description) */
.box-content {
  font-size: 1.1rem;
  color: #e0e0e0;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

/* Rainbow Text Animation for Demo Links */
.rainbow {
  background: linear-gradient(90deg, #ff00ff, #00f7ff, #ff00ff, #00f7ff);
  background-size: 200% auto;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: rainbow-text 3s linear infinite;
}

.rainbow-text_animated {
  font-size: 1.2rem;
  font-weight: 500;
  margin-top: 1rem;
  cursor: pointer;
  display: inline-block;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  transition: all 0.3s ease;
  text-decoration: none;
}

.rainbow-text_animated:hover {
  transform: translateY(-2px);
  text-shadow: 0 0 8px rgba(0, 247, 255, 0.7);
}

@keyframes rainbow-text {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

/* Blank Paragraph Spacing */
.blank-paragraph {
  margin: 0;
  height: 1rem;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .container {
    padding: 2rem 1rem;
  }

  .box {
    padding: 1.5rem;
  }

  .box-title {
    font-size: 1.5rem;
  }

  .box-content {
    font-size: 1rem;
  }
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Toggle Button Styling */
.toggle-button {
  position: fixed;
  top: 20px;
  right: 30px;
  padding: 10px 20px;
  background-color: #000;
  color: #fff;
  border: 2px solid white;
  border-radius: 40px;
  cursor: pointer;
  z-index: 1000;
  transition: all 0.3s ease;
}

.toggle-button:hover {
  transform: translateY(-5px);
  animation: rainbow-border 3s linear infinite;
}